<template>
  <div class="addResult mescroll">
    <div class="bg">
      <img src="../../assets/img/activity/bg3.png" alt />
    </div>
    <div class="mid">
      <img class="pic" src="../../assets/img/activity/success1.png" alt />
      <p class="state">恭喜你！活动发布成功。</p>
      <img class="qrcode" :src="$config.qrCode" alt="">
      <p class="text">关注【{{$config.activeTitle}}】公众号，管理您已发布的活动</p>
      <div class="btns">
        <div class="btn" @click="toActivity">查看活动</div>
        <div class="btn share" @click="shareShow=true">分享活动</div>
      </div>
    </div>
    <div class="mask" v-if="shareShow" @click="hideShareMask">
      <img src="../../assets/img/activity/shang.png" alt="">
    </div>
  </div>
</template>
<script>
import { getActivityDetail, wxApi } from "@/api";
export default {
  data() {
    return {
      detail: {},
      shareShow: false,
    };
  },
  methods: {
    hideShareMask() {
      this.shareShow = false;
    },
    toActivity() {
      this.$router.push({
        name: "shareActivity",
        query: {
          id: this.$route.query.id,
        },
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getActivityDetail({
        acode: this.$route.query.id,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          this.share();
        } else {
          this.$toast(res.msg);
        }
      });
    },
    share() {
      let that = this;
      wxApi()
        .then(res => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function() {
            that.$wx.onMenuShareAppMessage({
              title: that.detail.viewName, // 分享标题
              desc: that.detail.viewName2, // 分享描述
              link:
                that.$config.http +
                "/wx/#/shareActivity?parentId=" +
                sessionStorage.getItem("userId") +
                "&id=" +
                that.$route.query.id, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.aimg), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function() {
                // 用户确认分享后执行的回调函数
                that.shareShow = false;
                that.show = true;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.detail.viewName, // 分享标题
              link:
                that.$config.http +
                "/wx/#/shareActivity?parentId=" +
                sessionStorage.getItem("userId") +
                "&id=" +
                that.$route.query.id, // 分享链接
              imgUrl: that.$utils.dealImg(that.detail.aimg), // 分享图标
              success: function() {
                that.shareShow = false;
                that.show = true;
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch(err => {
          console.log(err);
        });
    },
  },
  mounted() {
    document.title = "新增结果";
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.addResult {
  .bg {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .mid {
    background: url("../../assets/img/activity/bg4.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 20px;
    height: 75vh;
    padding: 50px 0 0;
    top: 30px;
    .pic {
      width: 60px;
      height: 60px;
    }

    .state {
      line-height: 30px;
      color: #666;
      font-size: 14px;
    }
    .qrcode {
      margin: 40px 0 0;
      width: 50vw;
    }
    .text {
      line-height: 30px;
      font-size: 14px;
    }
    .btns {
      display: flex;
      align-items: center;
      .btn {
        flex: 1;
        background: $base-color;
        border-radius: 5px;
        height: 50px;
        line-height: 50px;
        color: #fff;
        margin: 20px 10px 0;
        font-size: 15px;
      }
      .share {
        background: #07b438;
      }
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    img {
      position: fixed;
      top: 10px;
      right: 10px;
      width: 100px;
    }
  }
}
</style>
